'use strict';

import * as MediaSourceUtils from './utils/mediaSource.js';
import { loadData } from './utils/utils.js';
import { toUtf8 } from './utils/utf8.js';
import { base64urlEncode } from './utils/base64.js';

var MEDIA_ERROR_CODES = ['', 'MEDIA_ERR_ABORTED', 'MEDIA_ERR_NETWORK', 'MEDIA_ERR_DECODE', 'MEDIA_ERR_SRC_NOT_SUPPORTED', 'MEDIA_ERR_ENCRYPTED'];

/*------------------------------------------------------------------------------------------*/

// ENCRYPTED MEDIA EXTENSIONS TESTS

/*------------------------------------------------------------------------------------------*/

/**
* Test if EME API is supported (MediaKeys defined)
* @return {Promise} a promise resolved with true or false
*/
export function eme_tests_support_eme() {

    return new Promise(function (resolve) {
        var hasWebKitMediaKeys = ("WebKitMediaKeys" in window),
            hasMsMediaKeys = ("MSMediaKeys" in window),
            hasMediaKeys = ("MediaKeys" in window);
        resolve(hasWebKitMediaKeys || hasMsMediaKeys || hasMediaKeys);
    });
}

/**
* Test if EME API is enabled, i.e. if not disabled due to insecure origins
* @return {Promise} a promise resolved with true or false
*/
export function eme_tests_eme_enabled() {
    var hasWebKitMediaKeys = ("WebKitMediaKeys" in window),
        hasMsMediaKeys = ("MSMediaKeys" in window),
        hasMediaKeys = ("MediaKeys" in window);

    if (hasMediaKeys) {
        return new Promise(function (resolve) {
            resolve(navigator.requestMediaKeySystemAccess !== undefined &&  typeof navigator.requestMediaKeySystemAccess === 'function');
        });    
    } else if (hasWebKitMediaKeys || hasMsMediaKeys) {
        return new Promise(function (resolve) {
            resolve(true);
        }); 
    } else {
        return new Promise(function (resolve) {
            resolve(false);
        });         
    }
}

/**
* Returns a list of supported CDMs
* @return {Promise} a promise resolved with an array of results ( {type: type, supported: true/false })
*/
export function eme_get_supported_cdm() {
    return new Promise(function (resolve) {

        var supportedCDMs = [];
        var CDM = [
            'org.w3.clearkey',
            'com.widevine.alpha',
            'com.microsoft.playready'
        ];

        var initDataTypes = ['cenc'];

        eme_tests_support_key_system(CDM, initDataTypes).then(function (results) {
            results.forEach(function (result) {
                if (result.supported) {
                    supportedCDMs.push(result.keySystem);
                }
            });
            resolve(supportedCDMs);
        });
    });
}

/**
* Test support for a list of CDMs
* @param {array} keySystems The key systems to test
* @param {array} initDataTypes The init data types ('cenc', 'webm' or 'keyids')
* @return {Promise} a promise resolved using an array of object {supported:true/false, keySystem : the key system, type: init data type,err: the error if any}
*/
export function eme_tests_support_key_system(keySystems, initDataTypes) {

    return new Promise(function (resolve) {

        var results = [];
        var keySystemPromises = [];

        keySystems.forEach(function (keySystem) {
            var eachPromise = new Promise(function (resolve) {

                var dataTypesPromises = [];

                initDataTypes.forEach(function (initDataType) {
                    var eachDataTypePromise = new Promise(function (resolve) {

                        eme_tests_single_support_key_system(
                            keySystem, initDataType).then(function (result) {
                            results.push(result);
                            resolve(result);
                        });

                    });
                    dataTypesPromises.push(eachDataTypePromise);
                    return eachDataTypePromise;
                });

                Promise.all(dataTypesPromises).then(function () {
                    resolve();
                });

            });

            keySystemPromises.push(eachPromise);
            return eachPromise;
        });


        Promise.all(keySystemPromises).then(function () {
            resolve(results);
        });
    });
}

/**
* Test if init data type is supported for specified key system
* @param {string} keySystem key system
* @param {string} initDataType ('cenc', 'webm' or 'keyids')
* @return {Promise} a promise resolved using {supported:true/false, keySystem : the key system, type: init data type,err: the error if any}
*/
export function eme_tests_single_support_key_system(keySystem, initDataType) {
    var hasWebKitMediaKeys = ("WebKitMediaKeys" in window),
        hasMsMediaKeys = ("MSMediaKeys" in window),
        hasMediaKeys = ("MediaKeys" in window);

    return new Promise(function (resolve) {
        if (hasMediaKeys) {
            navigator.requestMediaKeySystemAccess(
                keySystem, getKeySystemConfiguration(initDataType)).then(function () {
                resolve({
                    supported: true,
                    keySystem: keySystem,
                    type: initDataType,
                    err: ''
                });
            }, function (err) {
                resolve({
                    supported: false,
                    keySystem: keySystem,
                    type: initDataType,
                    err: err
                });
            });    
        } else if (hasWebKitMediaKeys || hasMsMediaKeys) {
            try {
                new window[hasWebKitMediaKeys ? 'WebKitMediaKeys' : 'MSMediaKeys'](keySystem);
                resolve({
                    supported: true,
                    keySystem: keySystem,
                    type: initDataType,
                    err: ''
                });
            } catch (e) {
                resolve({
                    supported: false,
                    keySystem: keySystem,
                    type: initDataType,
                    err: e
                });                
            }
        }
    });
}

/**
* Test encrypted media data appending and playback with specified key systems
* @param {array} keysystems the key systems to be tested
* @param {object} drmConfig some DRM configuration info
* @param {object} data the media segment info
* @param {boolean} initSegmentOnly true to push only init data
* @return {Promise} a promise resolved using an array of object {appended:true/false, keySystem : the key system, err: the error if any}
*/
export function eme_test_append_data(keySystems, drmConfig, data, initSegmentOnly) {
    return new Promise(function (resolve) {
        var results = [];
        var keySystemPromises = [];

        keySystems.forEach(function (keySystem) {
            var eachPromise = new Promise(function (resolve) {
                eme_test_single_append_data(keySystem, drmConfig, data, initSegmentOnly).then(function (result) {
                    results.push(result);
                    resolve();
                });
            });

            keySystemPromises.push(eachPromise);
            return eachPromise;
        });

        Promise.all(keySystemPromises).then(function () {
            resolve(results);
        });
    });
}

/**
* Test encrypted media data appending and playback with specified key systems
* @param {string} keysystem the key system to be tested
* @param {object} drmConfig some DRM configuration info
* @param {object} data the media segment info
* @param {boolean} initSegmentOnly true to push only init data
* @return {Promise} a promise resolved using an array of object {appended:true/false, keySystem : the key system, err: the error if any}
*/
export function eme_test_single_append_data(keysystem, drmConfig, data, initSegmentOnly) {
    return new Promise(function (resolve) {
        eme_playback_test(keysystem, drmConfig, data, initSegmentOnly).then(function () {
            resolve({
                appended: true,
                keySystem: keysystem,
                err: ''
            });
        }, function (err) {
            resolve({
                appended: false,
                keySystem: keysystem,
                err: err
            });
        });
    });
}

/*------------------------------------------------------------------------------------------*/

// ENCRYPTED MEDIA EXTENSIONS TESTS TOOLS

/*------------------------------------------------------------------------------------------*/
var CLEARKEY_CDM = 'org.w3.clearkey';

function getInitData(contentitem, initDataType) {
    if (initDataType == 'webm') {
        return new Uint8Array(contentitem.keys[0].kid); // WebM initData supports only a single key
    }

    if (initDataType == 'cenc') {

        var size = 36 + contentitem.keys.length * 16,
            kids = contentitem.keys.map(function (k) {
                return k.kid;
            });

        return new Uint8Array(Array.prototype.concat.call([
            0x00, 0x00, size / 256, size % 256, // size
            0x70, 0x73, 0x73, 0x68, // 'pssh'
            0x01, // version = 1
            0x00, 0x00, 0x00, // flags
            0x10, 0x77, 0xEF, 0xEC, 0xC0, 0xB2, 0x4D, 0x02, // Common SystemID
            0xAC, 0xE3, 0x3C, 0x1E, 0x52, 0xE2, 0xFB, 0x4B,
            0x00, 0x00, 0x00, kids.length], // key count ]
            Array.prototype.concat.apply([], kids), [0x00, 0x00, 0x00, 0x00] // datasize
        ));
    }
    if (initDataType == 'keyids') {

        return toUtf8({
            kids: contentitem.keys.map(function (k) {
                return base64urlEncode(new Uint8Array(k.kid));
            })
        });
    }
    throw 'initDataType ' + initDataType + ' not supported.';
}

// Returns a MediaKeySystemConfiguration for |initDataType| that should be
// accepted, possibly as a subset of the specified capabilities, by all
// user agents.
function getKeySystemConfiguration(initDataType, codec) {
    var contentType = codec ? codec : 'video/mp4;codecs=\"avc1.4d401e\"';
    var robustnesses = [
        'SW_SECURE_CRYPTO',
        'SW_SECURE_DECODE',
        'HW_SECURE_CRYPTO',
        'HW_SECURE_DECODE',
        'HW_SECURE_ALL'
    ];

    var confs = [];
    for (var i = 0; i < robustnesses.length; i++) {
        confs.push({
            initDataTypes: [initDataType],
            videoCapabilities: [{
                contentType: contentType,
                robustness: robustnesses[i]
            }],
            sessionTypes: ['temporary']
        });
    }

    return confs;
}

function testmediasource(config) {

    return new Promise(function (resolve, reject) {
        MediaSourceUtils.openMediaSource(config.video).then(function (mediaInfo) {
            loadData(config.segmentPath).then(function (mediaData) {
                if (config.initSegmentOnly) {
                    mediaData = mediaData.subarray(config.segment.init.offset, config.segment.init.offset + config.segment.init.size);
                }
                MediaSourceUtils.appendData(mediaInfo.mediaTag, mediaInfo.mediaSource, mediaData, config.segmentType).then(function () {
                    resolve();
                }, function (err) {
                    reject(err.message);
                });
            }, function (err) {
                reject(err.message);
            });
        });
    });
}

function eme_playback_test(keysystem, drmConfig, data, initSegmentOnly) {
    if (keysystem === CLEARKEY_CDM) {
        return playback_clear_key_test(data, initSegmentOnly);
    } else {
        return playback_drm_test(keysystem, drmConfig, data, initSegmentOnly);
    }
}

function playback_clear_key_test(data, initSegmentOnly) {
    var contentitem = data,
        handler = /*drmConfig ? new DrmMessageHandler(keysystem, drmConfig, contentitem):*/ null;

    var config = {
        keysystem: CLEARKEY_CDM,
        messagehandler: handler ? handler.messagehandler : null,
        segment: contentitem,
        segmentPath: contentitem.url,
        segmentType: contentitem.type,
        initDataType: 'keyids',
        initData: getInitData(contentitem, 'keyids'),
        initSegmentOnly: initSegmentOnly
    };

    return start_eme_playback_test(config);
}

function playback_drm_test(keysystem, drmConfig, data, initSegmentOnly) {

    var contentitem = data,
        handler = /*drmConfig ? new DrmMessageHandler(keysystem, drmConfig, contentitem):*/ null;

    var config = {
        keysystem: keysystem,
        serverCertificate: (drmConfig && drmConfig[keysystem]) ? drmConfig[keysystem].serverCertificate : null,
        messagehandler: handler ? handler.messagehandler : null,
        segment: contentitem,
        segmentPath: contentitem.url,
        segmentType: contentitem.type,
        initDataType: contentitem.initDataType,
        initSegmentOnly: initSegmentOnly
    };

    return start_eme_playback_test(config);
}

function start_eme_playback_test(config) {

    return new Promise(function (resolve, reject) {
        var mediaTag = document.createElement("video");
        if (!document.body) {
            document.body = document.createElement("body");
        }
        document.body.appendChild(mediaTag);

        config.video = mediaTag;
        playback(config).then(function () {
            document.body.removeChild(mediaTag);
            resolve(true);
        }, function (err) {
            document.body.removeChild(mediaTag);
            reject(err);
        });
    });
}

function playback(config) {

    var ksConfiguration = getKeySystemConfiguration(config.initDataType, config.segmentType);

    return new Promise(function (resolve, reject) {

        var _video = config.video,
            _mediaKeys = null,
            _mediaKeySession = null,
            _timeout = null;

        function reset() {
            return new Promise(function (resolve, reject) {
                if (_timeout !== null) {
                    clearTimeout(_timeout);
                    _timeout = null;
                }

                console.log('### Reset video src');
                _video.removeAttribute('src');
                _video.load();

                console.log('### Reset video MediaKeys');
                _video.setMediaKeys(null).then(function() {
                    if (_mediaKeySession != null) {
                        console.log('### Reset video MediaKeys');
                        _mediaKeySession.close();
                    }
                    resolve();
                });
            });
        }

        function onFailure(error) {
            reset().then(function() {
                reject(error);
            });
        }

        function onSuccess() {
            reset().then(function() {
                resolve();
            });
        }

        function onEncrypted(event) {

            // Only create the session for the first encrypted event
            if (_mediaKeySession !== null) {
                return;
            }

            var initDataType = config.initData ? config.initDataType : event.initDataType;
            var initData = config.initData || event.initData;

            _mediaKeySession = _mediaKeys.createSession('temporary');
            _mediaKeySession.addEventListener('message', onMessage, true);
            _mediaKeySession.generateRequest(initDataType, initData).catch(onFailure);
        }

        function onMessage(event) {
            if (config.messagehandler) {
                config.messagehandler(event.messageType, event.message).then(function (response) {
                    return event.target.update(response);
                }).catch(onFailure);    
            }
        }

        function onPlaying() {
            onSuccess();
        }

        function onError(/*error*/) {
            var error = MEDIA_ERROR_CODES[_video.error.code];
            if (_video.error.message) {
                error += ': ' + _video.error.message;
            }
            onFailure(error);
        }

        function onTimeout() {
            _timeout = null;
            if (!_mediaKeySession) {
                // No MediaKeySession has been created, i.e. no 'encrypted' event has been received
                onFailure('content is encrypted but no \'encrypted\' event has been raised');
            } else {
                // Timeout: we assume that no error happened, but 'playing' event
                // may not be raised on Android devices because of human gesture requirement
                onSuccess();
            }
        }

        navigator.requestMediaKeySystemAccess(config.keysystem, ksConfiguration).then(function (access) {
            return access.createMediaKeys();
        }).then(function (mediaKeys) {
            _mediaKeys = mediaKeys;
            if (config.serverCertificate && _mediaKeys.setServerCertificate) {
                _mediaKeys.setServerCertificate(new Uint8Array(config.serverCertificate).buffer).then(function () {
                    return _video.setMediaKeys(_mediaKeys);
                }).catch(function (error) {
                    onFailure('failed to set server certificate');
                });
            } else {
                return _video.setMediaKeys(_mediaKeys);
            }
        }).then(function () {
            _video.addEventListener('encrypted', onEncrypted, true);
            _video.addEventListener('playing', onPlaying, true);
            _video.addEventListener('error', onError, true);
            return testmediasource(config);
        }).then(function () {
            if (config.initSegmentOnly) {
                onSuccess();
            } else {
                // wait for video playing to pass the test
                _timeout = setTimeout(onTimeout, 500);
                if (config.messagehandler) {
                    _video.play();
                }
            }
        }).catch(onFailure);
    });
}
