var DRM_CONFIG = {
    'com.widevine.alpha': {
        serverCertificate: wv_cert_prod
    }
}

// List of test functions to playback clear contents
var test_mse_funcs = [
    checkMSESupport,
    checkCodecsSupport,
    checkAppendInitSegment,
    checkAppendMediaSegment
];

// List of test functions to playback encrypted contents
var test_eme_funcs = test_mse_funcs.concat([
    checkEMESupport,
    checkEMEAvailability,
    checkCDMAvailability,
    checkAppendEncryptedInitSegment,
    checkAppendEncryptedMediaSegment
]);

// List of test functions to playback contents on IE11
var test_eme_funcs_IE11 = test_mse_funcs.concat([
    checkEMESupport,
    checkEMEAvailability,
    checkCDMAvailability
]);

// List of test functions to playback HLS contents on Safari
var test_funcs_safari = [
    checkEMESupport,
    checkEMEAvailability,
    checkFairPlayCDMAvailability
];

var userAgent = navigator.userAgent.toLowerCase();
var isSafari = /version[\/\s](\d+\.\d+)/.test(userAgent);
var isIE11 = /trident/.test(userAgent);
var supportedKeySystem = null;
var setServerCertificate = true;
var DEBUG = true;

function log(message) {
    if (!DEBUG) {
        return;
    }
    console.log('[HasPlayerTest] ' + message);
}

function checkMSESupport() {
    log('checkMSESupport');
    return new Promise(function (resolve) {
        mse_test_supports_media_source().then(function (supports) {
            resolve ({
                canPlay: supports,
                error: supports ? undefined : {
                    code: CAPABILITY_ERR_MEDIASOURCE,
                    message: 'MSE is not supported'
                }
            });
        });
    });
}

function checkCodecsSupport() {
    log('checkCodecsSupport');
    return new Promise(function (resolve) {
        mse_test_type_support(CODECS).then(function (results) {
            var supported = true;
            for (var i = 0; i < results.length && supported; i++) {
                if (!results[i].supported) {
                    supported = false;
                    resolve({
                        canPlay: false,
                        error: {
                            code: MEDIA_ERR_CODEC_UNSUPPORTED,
                            message: 'Codec not supported: ' + results[i].type
                        }
                    });
                }
            }
            if (supported) {
                resolve({canPlay: true});
            }
        });
    });
}

function checkAppendInitSegment() {
    log('checkAppendInitSegment');
    return new Promise(function (resolve) {
        mse_test_append_data(MSE_SEGMENT_INFO, true).then(function (result) {
            resolve ({
                canPlay: result.appended,
                error: result.appended ? undefined : {
                    code: MEDIA_ERR_SRC_NOT_SUPPORTED,
                    message: 'Failed to append initialization segment: ' + result.err
                }
            });
        });
    });
}

function checkAppendMediaSegment() {
    log('checkAppendMediaSegment');
    return new Promise(function (resolve) {
        mse_test_append_data(MSE_SEGMENT_INFO).then(function (result) {
            resolve ({
                canPlay: result.appended,
                error: result.appended ? undefined : {
                    code: MEDIA_ERR_DECODE,
                    message: 'Failed to append media segment: ' + result.err
                }
            });
        });
    });
}

function checkEMESupport() {
    log('checkEMESupport');
    return new Promise(function (resolve) {
        eme_tests_support_eme().then(function (supports) {
            resolve ({
                canPlay: supports,
                error: supports ? undefined : {
                    code: CAPABILITY_ERR_MEDIAKEYS,
                    message: 'EME is not supported'
                }
            });
        });
    });
}
    
function checkEMEAvailability() {
    log('checkEMEAvailability');
    return new Promise(function (resolve) {
        eme_tests_eme_enabled().then(function (enabled) {
            resolve({
                canPlay: enabled,
                error: enabled ? undefined : {
                    code: MEDIA_KEYSYSERR_ACCESS_DENIED,
                    message: 'No KeySystem/CDM available, Insecure origins are not allowed'
                }
            });
        });
    });
}

function checkKeySystemAvailability(keySystems) {
    log('checkKeySystemAvailability: ' + keySystems);
    return new Promise(function (resolve) {
        eme_tests_support_key_system(keySystems, INITDATA_TYPES).then(function (results) {
            var supported = false;
            for (var i = 0; i < results.length && !supported; i++) {
                if (results[i].supported) {
                    supported = true;
                    // Store supported key system for appending encrypted initialization/media segment
                    supportedKeySystem = results[i].keySystem;
                    resolve({canPlay: true});
                }
            }
            if (!supported) {
                resolve({
                    canPlay: false,
                    error: {
                        code: MEDIA_KEYSYSERR_ACCESS_DENIED,
                        message: 'No KeySystem is available'
                    }
                });
            }
        });
    });
}

function checkCDMAvailability() {
    return checkKeySystemAvailability(KEY_SYSTEMS);
}

function checkFairPlayCDMAvailability() {
    return checkKeySystemAvailability(['com.apple.fps.1_0']);
}

function checkAppendEncryptedInitSegment() {
    log('checkAppendEncryptedInitSegment');
    return new Promise(function (resolve) {
        eme_test_append_data([supportedKeySystem], setServerCertificate ? DRM_CONFIG : null, EME_SEGMENT_INFO, false/*initSegmentOnly*/).then(function (results) {
            resolve({
                canPlay: results[0].appended,
                error: results[0].appended ? undefined : {
                    code: MEDIA_ERR_SRC_NOT_SUPPORTED,
                    message: 'Failed to append initialization segment: ' + results[0].err
                }
            });
        });
    });    
}

function checkAppendEncryptedMediaSegment() {
    log('checkAppendEncryptedMediaSegment');
    return new Promise(function (resolve) {
        eme_test_append_data([supportedKeySystem], setServerCertificate ? DRM_CONFIG : null, EME_SEGMENT_INFO, false/*FullSegment*/).then(function (results) {
            resolve({
                canPlay: results[0].appended,
                error: results[0].appended ? undefined : {
                    code: MEDIA_ERR_DECODE,
                    message: 'Failed to append encrypted media segment: ' + results[0].err
                }
            });
        });
    });    
}


function executeTestFunctions(funcs) {
    return new Promise(function (resolve) {
        funcs.reduce((p, fn) => {
            return p.then(result => {
                if (result) {
                    log('result: '+ JSON.stringify(result));
                }
                if (result && !result.canPlay) {
                    resolve(result);
                } else {
                    return fn();
                }
            });
        }, Promise.resolve()).then(result => {
            log('Done');
            // result from last function
            if (result) {
                log('result: '+ JSON.stringify(result));
                resolve(result);
            }
        });
    });
}

/**
* Returns the version number.
* @return {string} the version number
*/
export function getVersion () {
    return VERSION;
}

/**
* Returns the full version number (including Git tag).
* @return {string} the version number including Git tag
*/
export function getVersionFull () {
    if (GIT_TAG.indexOf("@@") === -1) {
        return VERSION + '_' + GIT_TAG;
    } else {
        return VERSION;
    }
}

/**
* Returns the build date.
* @return {string} build date
*/
export function getBuildDate () {
    if (BUILD_DATE.indexOf("@@") === -1) {
        return BUILD_DATE;
    } else {
        return 'Not a builded version';
    }    
}

/**
* Test if hasplayer.js can be used to play contents
* @param {boolean} encrypted true if check support for encrypted contents, false if check only support for clear contents
* @param {String} protocol the specific HAS protocol to test (for example 'HLS')  
* @param {boolean} setCert true if server certificate has to be protactively set before creating EME MediaKeySessions (true by default)
* @return {Promise} a promise resolved using an object {canPlay: true, error: the error if any as an object {code: the error code, message: the error message} }
*/
export function hasplayerCanPlay(encrypted, protocol, setCert) {

    // Set server certificate by default
    setServerCertificate = (setCert !== undefined) ? setCert : true;
    
    return new Promise(function (resolve) {
        // Specific use case for HLS
        if (protocol === 'HLS') {
            if (encrypted === false) {
                if (isSafari) {
                    // On Safari, clear HLS content are supported by native <video> element
                    resolve({
                        canPlay: true
                    });
                } else {
                    // On other browsers, check if MSE is supported
                    executeTestFunctions(test_mse_funcs).then(function (result) {
                        resolve(result);
                    }); 
                }
            } else {
                // For encrypted contents, check if EME and FairPlay CDM are available (Safari/OSx)
                executeTestFunctions(test_funcs_safari).then(function (result) {
                    resolve(result);
                });                
            }
        } else {
            executeTestFunctions(encrypted ? (isIE11 ? test_eme_funcs_IE11 : test_eme_funcs) : test_mse_funcs).then(function (result) {
                resolve(result);
            });
        }
    });
}
    
