import {
    mse_test_supports_media_source,
    mse_get_supported_codecs,
    mse_test_type_support,
    mse_test_append_data} from './src/mseTests.js';

import {
    eme_tests_support_eme,
    eme_tests_eme_enabled,
    eme_get_supported_cdm,
    eme_tests_support_key_system,
    eme_tests_single_support_key_system,
    eme_test_append_data,
    eme_test_single_append_data} from './src/emeTests.js';

import {
    getVersion,
    getVersionFull,
    getBuildDate,
    hasplayerCanPlay} from './src/hasplayerTests.js';

// Shove both of these into the global scope
var context = (typeof window !== 'undefined' && window) || global;

var mseemecapabilities = context.mseemecapabilities;
if (!mseemecapabilities) {
    mseemecapabilities = context.mseemecapabilities = {};
}

mseemecapabilities.mse_test_supports_media_source = mse_test_supports_media_source;
mseemecapabilities.mse_get_supported_codecs = mse_get_supported_codecs;
mseemecapabilities.mse_test_type_support = mse_test_type_support;
mseemecapabilities.mse_test_append_data = mse_test_append_data;
mseemecapabilities.eme_tests_support_eme = eme_tests_support_eme;
mseemecapabilities.eme_tests_eme_enabled = eme_tests_eme_enabled;
mseemecapabilities.eme_get_supported_cdm = eme_get_supported_cdm;
mseemecapabilities.eme_tests_support_key_system = eme_tests_support_key_system;
mseemecapabilities.eme_tests_single_support_key_system = eme_tests_single_support_key_system;
mseemecapabilities.eme_test_append_data = eme_test_append_data;
mseemecapabilities.eme_test_single_append_data = eme_test_single_append_data;
mseemecapabilities.getVersion = getVersion;
mseemecapabilities.getVersionFull = getVersionFull;
mseemecapabilities.getBuildDate = getBuildDate;
mseemecapabilities.hasplayerCanPlay = hasplayerCanPlay;

export default mseemecapabilities;
export {
    mse_test_supports_media_source,
    mse_get_supported_codecs,
    mse_test_type_support,
    mse_test_append_data,
    eme_tests_support_eme,
    eme_tests_eme_enabled,
    eme_get_supported_cdm,
    eme_tests_support_key_system,
    eme_tests_single_support_key_system,
    eme_test_append_data,
    eme_test_single_append_data,
    getVersion,
    getVersionFull,
    getBuildDate,
    hasplayerCanPlay
};